import styled from 'styled-components';
import Card from "./Perguntas-Card"

const PergSection01 = styled.div`
  padding: 5% 2%;
  width: 96%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  align-items: center;
  gap: 10vh;
`;

const PergSection01Texto = styled.div`
  gap: 2vw;
  display: flex;
  flex-direction: column;
`;

const PergSection01H1 = styled.h1`
  color: #5d5a88;
  text-align: center;
  font-feature-settings: 'clig' off, 'liga' off;
  font-family: DM Sans;
  font-size: clamp(2.188rem, 1.477rem + 1.894vw, 3.75rem);
  font-style: normal;
  font-weight: 700;
  line-height: normal;
`;

const PergSection01P = styled.p`
  color: #9795b5;
  text-align: center;
  font-feature-settings: 'clig' off, 'liga' off;
  font-family: DM Sans;
  font-size: clamp(0.75rem, 0.381rem + 0.985vw, 1.563rem);
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: justify;
`;

const PergSection01Perguntas = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2vw;
`;



function Perguntas() {
        const perguntas = [
          {
            Titulo: 'Como receber alertas em tempo real?',
            SubTitulo: 'Como os alertas so recebidos:',
            Conteudo: 'Para receber alertas em tempo real, voc pode se inscrever em nossa plataforma e configurar suas preferncias de notificao. Garantimos que voc seja informado sempre que houver mudanas significativas nas condies das enchentes.'
          },
          {
            Titulo: 'As informaes fornecidas so confiveis?',
            SubTitulo: 'De onde vm os dados:',
            Conteudo: 'Sim, nossa equipe se empenha em coletar e fornecer informaes precisas e confiveis. Trabalhamos com fontes confiveis de dados e atualizamos constantemente nossas informaes para garantir a preciso.'
          },
          {
            Titulo: "Como posso contribuir com o P d'gua?",
            SubTitulo: 'Compartilhe suas observaes:',
            Conteudo: 'Agradecemos pelo interesse em contribuir! Voc pode compartilhar suas prprias observaes e relatos sobre enchentes em sua regio. Essas contribuies podem ajudar a melhorar nossa compreenso das condies locais.'
          },
          {
            Titulo: "O servio do P d'gua  gratuito?",
            SubTitulo: 'Recursos gratuitos:',
            Conteudo: 'Sim, muitos dos nossos recursos e informaes so gratuitos para os usurios. No entanto, tambm oferecemos opes premium com recursos adicionais para aqueles que buscam informaes mais avanadas e personalizadas.'
          },
        ];

    return (
        <div className="Perguntas">
            <PergSection01>
                <PergSection01Texto>
                    <PergSection01H1>Duvidas frequentes</PergSection01H1> 
                    <PergSection01P>Aqui voc encontrar respostas para algumas das perguntas mais comuns sobre nossos servios. <br></br> <br></br>Se voc no encontrar a resposta que procura, sinta-se  vontade para entrar em contato conosco diretamente.</PergSection01P>
                </PergSection01Texto>
                <PergSection01Perguntas>
                    {perguntas.map((pergunta, index) => (
                        <Card key={index} Titulo={pergunta.Titulo} SubTitulo={pergunta.SubTitulo} Conteudo={pergunta.Conteudo}/>
                    ))}
                </PergSection01Perguntas>
            </PergSection01>
        </div>
    );
}

export default Perguntas;



